import { createCustomElement } from "@servicenow/ui-core";
import snabbdom from "@servicenow/ui-renderer-snabbdom";
import view from "./view";
import styles from "./styles.scss";

/**
 * Displays the pricing details (price, recurring price & recurring frequency)
 * of given Catalog item or Order guide.
 *
 * ```jsx
 * <sn-catalog-price
 *		price={...}
 *		recurringPrice={...}
 *		visible={...}
 *		viewType={...}
 *		/>
 * ```
 *
 * @seismicElement sn-catalog-price
 * @summary Displays the pricing details (price, recurring_price, recurring frequency) of given Catalog item or Order guide
 *
 */
createCustomElement("sn-catalog-price", {
	renderer: { type: snabbdom },
	view,
	properties: {
		/**
		 * Label header to be shown for price details
		 *
		 * @type {string}
		 */
		label: {
			default: "Price"
		},

		/**
		 * Price display value of the corresponding Item/Order guide,
		 *
		 * @type {string}
		 */
		price: {
			default: "$4279.99"
		},

		/**
		 * Recurring price display value of the corresponding Item/Order guide,
		 *
		 * @type {string|Array.<string>}
		 */
		recurringPrice: {
			default: ["$100.00 Annually", "$200.00 Monthly"]
		},

		/**
		 * Whether to show price or not
		 *
		 * @type {boolean}
		 */
		visible: {
			default: true
		},
		/**
		 * View type of the price component, which has following choices
		 * stacked: Shows price v titles in a stacked form
		 * tabbed: Shows price v titles in tabbed form
		 *
		 * @type {string}
		 */
		viewType: {
			default: "stacked"
		}
	},
	styles
});
