import { t } from "sn-translate";

const renderRecurringPrice = (price, recurringPrice) => {
	return Array.isArray(recurringPrice)
		? recurringPrice.map((item, i) => renderBlock(i, item, price))
		: renderBlock(0, recurringPrice, price);
};

const renderBlock = (index, recurringPrice, price) => {
	return (
		<div className="recurring-price-value">
			<span>{index == 0 && (!price || !recurringPrice) ? "" : " + "}</span>
			{recurringPrice}
		</div>
	);
};

const renderStackedPrice = (label, price, recurringPrice) => {
	return (
		<div className="price-stacked-container">
			<div className="price-label">{label}</div>
			<div className="price-value">{price}</div>
			<div className="recurring-price-value-container">
				{renderRecurringPrice(price, recurringPrice)}
			</div>
		</div>
	);
};

const renderTabbedPrice = (label, price, recurringPrice) => {
	return (
		<div className="price-tabbed-container">
			<div className="price-label">{label}</div>
			<div className="price-right-container">
				<div className="price-value">{price}</div>
				<div className="recurring-price-value-container">
					{renderRecurringPrice(price, recurringPrice)}
				</div>
			</div>
		</div>
	);
};

const view = ({
	properties: { label, price, recurringPrice, visible, viewType }
}) => {
	if (visible && (price || recurringPrice)) {
		label = label || t("Price");
		return (
			<div className="price-container">
				{viewType == "tabbed"
					? renderTabbedPrice(label, price, recurringPrice)
					: renderStackedPrice(label, price, recurringPrice)}
			</div>
		);
	}
};

export default view;
